.task-container {
    width: 500px;
    min-height: 65px;
    background-color: #1b1b32;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1ps solid white;
    cursor: pointer;
}

.task-container:nth-child(3n + 1) {
    background-color: #1b1b32;
}

.task-container:nth-child(3n + 2) {
    background-color: #2a2a40;
}

.task-container:nth-child(3n + 3) {
    background-color: #3b3b4f;
}

.task-text {
    width: 100%;
    height: 100%;
    font-size: 20px;
    display: flex;
    align-items: center;
    overflow-wrap: anywhere;
    color: white;
}

.task-icon {
    width: 30px;
    height: 30px;
    margin: 5px;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}

.task-container.completed {
    width: 500px;
    min-height: 65px;
    background-color: #5a01a7;
    text-decoration: line-through;
    margin: 5px 0;
    padding: 8px 15px 8px 20px;
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-radius: 7px;
    border: 1ps solid white;
    cursor: pointer;
}